.score {
  background-color: #fff;
  width: 80px;
  padding: 10px 0;
  text-align: center;
  border-radius: 0.5em;
  text-transform: uppercase;
}
.score p {
  font-size: 40px;
  color: #565468;
  font-weight: 700;
  line-height: 0.9em;
}
.score small {
  color: #2a45c2;
  font-size: 10px;
}
